<template>
  <div class="hello">
    <b-modal :active.sync="isLoginModalActive" has-modal-card canCancel="false">
      <div class="card" style="padding: 10px">
        <h1 style="font-size: 18pt; font-weight: bold;">Login</h1>
        <b-field label="Name">
          <b-input v-model="loginUserName"></b-input>
        </b-field>
        <b-field label="Password" >
          <b-input type="password" v-model="loginPassword"></b-input>
        </b-field>
        <b-field label="Site Name" >
          <b-input v-model="loginSiteName"></b-input>
        </b-field>
        <button class="button is-medium is-primary saveButton" @click="login">
            Login
        </button>
        <div style="clear: both;"></div>
      </div>
    </b-modal>
    <nav class="navbar" role="navigation" aria-label="main navigation" style="color:white; background-color: #7957D5">
      <div class="navbar-brand">
        Admin
      </div>
    </nav>
    <table style="width: 100%;">
      <tr>
        <td>
          <aside class="menu" style="background: #CDCDCD">
            <p class="menu-label">
              Pages
            </p>
            <ul class="menu-list">
              <li v-for="page in config.fragmentedPages">
                <a @click="loadPage(page)">{{page.fragment}}</a>
              </li>
            </ul>
            <p class="menu-label">
              Documents
            </p>
            <ul class="menu-list">
              <li v-for="page in config.pages">
                <a @click="loadPage(page)">{{page}}</a>
              </li>
            </ul>
          </aside>
        </td>
        <td>
          <vue-editor v-model="editedPage"></vue-editor>
          <button class="button is-medium is-primary saveButton" @click="savePage">
              Save
          </button>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import GitHub from 'github-api';
import hbs from 'handlebars';
import { VueEditor } from 'vue2-editor'

var github;
var repository;

var getGithubFile = function(path, callback) {
  repository.getContents('master', path, false, function(err, contents) {
    if(err)
      console.error(err);

    callback(contents);
  });
}

export default {
  name: 'Index',
  data() {
    return {
      loginUserName: '',
      loginPassword: '',
      loginSiteName: '',
      isLoginModalActive: true,
      editedPagePath: undefined,
      editedPage: '',
      editedPageType: '',
      editedPageTemplate: '',
      config: []
    };
  },

  methods: {
    login() {
      github = new GitHub({
        username: this.loginUserName,
        password: this.loginPassword,
        auth: 'basic'
      });

      repository = github.getRepo(this.loginUserName, this.loginSiteName);

      alert(this.loginSiteName);

      this.loadRepoPages();

      this.isLoginModalActive = false;
    },

    savePage() {
      var _this = this;
      if(this.editedPageType === "fragment") {
        getGithubFile(this.editedPageTemplate, (responseText) => {
          var source = atob(responseText.content);
          var template = hbs.compile(source);

          var data = { "content": this.editedPage};
          var result = template(data);

          repository.writeFile(
           'master', // e.g. 'master'
           this.editedPagePath, // e.g. 'blog/index.md'
           this.editedPage, // e.g. 'Hello world, this is my new content'
           'edit ' + this.editedPagePath, // e.g. 'Created new index'
           function(err) {
            repository.writeFile(
             'master', // e.g. 'master'
             _this.editedPageDestinationPath, // e.g. 'blog/index.md'
             result, // e.g. 'Hello world, this is my new content'
             'compile ' + _this.editedPagePath, // e.g. 'Created new index'
             function(err) {}
            );
           }
          );


        });
      } else {
        // Creates a new file (or updates it if the file already exists)
        // with the content provided
        repository.writeFile(
         'master', // e.g. 'master'
         this.editedPagePath, // e.g. 'blog/index.md'
         this.editedPage, // e.g. 'Hello world, this is my new content'
         'edit ' + this.editedPagePath, // e.g. 'Created new index'
         function(err) {}
        );
      }
    },


    loadPage(page, type) {
      var _this = this;
      var tpl = '';
      var editedPageType = 'markdown';
      var dest;

      if(page.fragment) {
        tpl = page.template;
        dest = page.destination;
        page = page.fragment;

        editedPageType = 'fragment';
      }

      var xhr = new XMLHttpRequest();
      this.editedPageType = editedPageType;

      getGithubFile(page, function (responseText) {
        responseText = atob(responseText.content);
        var pageContent = responseText;

        _this.editedPagePath = page;
        _this.editedPage = responseText;
        _this.editedPageDestinationPath = dest;
        _this.editedPageTemplate = tpl;
      });
    },

    loadRepoPages() {
      var _this = this;
      getGithubFile('vue-git-cms.json', (responseText) => {
        var config = JSON.parse(atob(responseText.content));
        _this.config = config;
      });
    },
  },

  components: {
    VueEditor
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.navbar-brand {
  color: white;
  font-size: 24pt;
  font-weight: bold;
  padding-left: 12px;
}

.saveButton {
  float: right;
}
</style>



